import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DishListComponent } from './dish-list.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { AddComponent } from './add/add.component';
import { DetailComponent } from './detail/detail.component';
import { CreateDataDishesComponent } from './create-data-dishes/create-data-dishes.component';
import { ScrollingModule } from '@angular/cdk/scrolling';

const routes: Routes = [
  {
    path: '',
    component:  DishListComponent
  },
  {
    path: 'add',
    component:  AddComponent
  },
  {
    path: 'detail',
    component:  DetailComponent
  },
  {
    path: 'create-data',
    component:  CreateDataDishesComponent
  }
]

@NgModule({
  declarations: [DishListComponent, AddComponent, DetailComponent, CreateDataDishesComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    FormsModule,
    ReactiveFormsModule.withConfig({warnOnNgModelWithFormControl: 'never'}),
    TranslateModule,
    IonicModule,
    ScrollingModule
  ]
})
export class DishListModule { }
