import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { Platform } from '@ionic/angular';
declare var electron: any;
@Component({
  selector: 'app-create-data-dishes',
  templateUrl: './create-data-dishes.component.html',
  styleUrls: ['./create-data-dishes.component.scss']
})
export class CreateDataDishesComponent {
  formData: FormGroup[] = [];
  createCate: any = { status: false, statusBug: false, arrayErr: [], current: 0, total: 0, title: "" }
  url: any = "/assets/documents/list-dishes/" + this.languageService.translate("List of dishes") + ".xlsx"
  localProduct: any[] = this.vhQueryCafe.getlocalProducts().filter(item => item.type == 1);
  localCategory: any[] = this.vhQueryCafe.getlocalCategorys().filter(item => item.type == 1)
  nameFile: string = "";
  dataRestore: any = this.router.getCurrentNavigation().extras.state.dataRestore
  constructor(
    private router: Router,
    public languageService: LanguageService,
    private vhQueryCafe: VhQueryCafe,
    private vhComponent: VhComponent,
    private vhAlgorithm: VhAlgorithm,
    private nzModalService: NzModalService,
    private platform: Platform,
    private vhQuery: VhQuery,
  ) { }

  validation_messages = {
    name: [{ type: "required", message: this.languageService.translate("Name is required") }],
    status_name: [{ message: this.languageService.translate("Invalid name") }],
    price: [{ type: "required", message: this.languageService.translate("Price is required") },
    { type: "pattern", message: this.languageService.translate("Invalid price") }],
    id_category: [{ type: "pattern", message: this.languageService.translate("Invalid category") }]
  };
  /**
   * tạo form lưu các trường của món ăn từ file excel
   * @example let formArray = formArray.push(this.initForm(data,1))
   */
  initForm(data, index) {
    return new FormGroup({
      id: new FormControl(index),
      name: new FormControl(data[this.languageService.translate("_name_dishes")]),
      status_name: new FormControl(false), // trạng thái barcode true: trùng / false: oke
      category: new FormControl(data[this.languageService.translate("_menu")]),
      unit: new FormControl(data[this.languageService.translate("_unit")]),
      price: new FormControl(data[this.languageService.translate("_price")]),
      id_category: new FormControl(this.checkCategory(data[this.languageService.translate("_menu")])),
    })
  }

  /**Check lấy id danh mục */
  checkCategory(name) {
    if (this.localCategory.length && name != '') {
      let category = this.localCategory.find(item => item.name.toLowerCase().trim() == name.toLowerCase().trim())
      if (category) return category['id']
      else return null
    } else return ''
  }

  goBack() {
    this.router.navigate(['/cafe/dashboard/management/dishes/dish-list'], { state: this.dataRestore });
  }

  /** Tải file mẫu */
  downloadSampleFile() {
    if (this.platform.is("electron")){
      let urlEn = "https://github.com/Viethas-App/Download-Sample-File/releases/download/Download-Sample-File/List-of-dishes.xlsx"
      let urlVI = "https://github.com/Viethas-App/Download-Sample-File/releases/download/Download-Sample-File/Danh-sach-mon-an.xlsx"
      let appLang: any = localStorage.getItem("vh-cafe-language")
        if(appLang == 'vi') window.open(urlVI,'_blank', 'location=yes,height=250,width=250,scrollbars=yes,status=yes,title=Download')
          else window.open(urlEn,'_blank', 'location=yes,height=250,width=250,scrollbars=yes,status=yes,title=Download')
    } else {
      document.getElementById("download_product").click()
    }
}


  /**Chọn file */
  openFile() {
    document.getElementById("file_excel_product")['value'] = '';
    document.getElementById("file_excel_product").click()
    
}

  /**Check dữ liệu trước khi tạo dữ liệu */
  checkForm(): boolean {
    for (let i = 0; i < this.formData.length; i++) {
      if (this.formData[i].invalid || this.formData[i]['value'].status_name == true) {
        this.vhComponent.alertMessage(this.languageService.translate("Error"), "", `${this.languageService.translate("Error line")} ${i + 1}`, this.languageService.translate("Cancel"))
        return false;
      }
      if (i == this.formData.length - 1) {
        return true;
      }
    }
  }

  /**Mở CSVFile */
  openXLSXFile(events) {
    if (events.target.files[0]) {
      // lưu tên file
      this.nameFile = events.target.files[0]['name'].slice(0, events.target.files[0]['name'].lastIndexOf("."))
      this.vhAlgorithm.importXLSX(events.target.files[0]).then((data: any) => {
        if (data.length > 200) {
          this.vhComponent.alertMessage("", "", this.languageService.translate("Only 200 dishes can be imported at a time"), "OK").then(() => {
            this.formData = []
          })
        } else {
          this.formData = []
          if (data.length) {
            this.vhComponent.showLoading("").then(() => {
              for (let i in data) {
                if (data[i][this.languageService.translate("_name_dishes")] && data[i][this.languageService.translate("_price")])
                  this.formData.push(this.initForm(data[i], parseInt(i)))
                else {
                  this.vhComponent.hideLoading(0).then(() => this.fileFail())
                  break;
                }

                if (parseInt(i) == data.length - 1) {
                  this.vhComponent.hideLoading(0)
                  this.checkModelChange();
                }
              }
            })
          }
        }
      }).catch(err => this.vhComponent.hideLoading(0).then(() => this.fileFail()))
    }
  }

  createdError: string = ""
  createData() {
    if (this.checkForm()) {
      this.vhComponent.alertConfirm(this.languageService.translate("Confirm"), "", this.languageService.translate("Create product data"), "OK", this.languageService.translate("Cancel"))
        .then(res => {
          this.createCategoriesAndProduct()
        }, () => { })
    }
  }



  /**Tạo dữ liệu danh mục và sản phẩm */
  createCategoriesAndProduct() {
    this.vhComponent.showLoading("", "current-loading").then(async () => {
      // lấy dữ liệu tên category
      const dataCategory: any[] = [...new Set(this.formData.map(item => item['value']['category']))];
      // tìm ra dữ liệu category chưa được tạo database
      let createdCategory: any[] = []; // danh sách danh mục được tạo
      dataCategory.forEach((item, index) => {
        if (item && !this.localCategory.some(cate => this.vhAlgorithm.changeAlias(cate.name.trim()).toLowerCase() == this.vhAlgorithm.changeAlias(item.trim()).toLowerCase())) {
          createdCategory.push({ name: item, type: 1 });
        }
      });

      try {
        // add danh mục
        const categories: any = await this.vhQueryCafe.addCategorys(createdCategory);
        if (categories) {
          this.localCategory = [...this.localCategory.concat([...categories])];
        }
        else {
          throw Error("Categories have been created failed");
        }

        // add sản phẩm
        var dataProducts: any[] = [];
        var dataProductsSubs: any[] = [];
        const products = this.formData.map(item => { return { ...item.value } });

        // Xử lý dữ liệu cho danh sách sản phẩm
        for(let i = 0; i < products.length; i++) {
          if (!dataProducts.find(pro => pro.name == products[i].name)) {
            const category: any = this.localCategory.find(cate => this.vhAlgorithm.changeAlias(cate['name'].trim()).toLowerCase() === this.vhAlgorithm.changeAlias(products[i]['category'].trim()).toLowerCase());
            let subproducts = products.filter(e => e.name === products[i].name);

            // Nếu KHÔNG có subs
            if (parseFloat(products[i]['price'])) {
              dataProducts.push({
                // allow_sell: true,
                delete_hidden: false,
                describe: '',
                id_category: category?._id,
                img: '',
                manysize: false,
                name: products[i]['name'].trim(),
                selling: false,
                time_warning: 0,
                type: 1,
                units: [{
                  price: parseFloat(products[i]['price']),
                  price2: parseFloat(products[i]['price']),
                  unit: products[i]['unit'] ? products[i]['unit'].trim() : '',
                  barcode: '',
                  ratio: 1,
                  default: true
                }]
              });
            }

            // Nếu CÓ subs
            else {
              let subs: any[] = products[i]['price'].split(',');
              dataProductsSubs.push({
                delete_hidden: false,
                describe: '',
                id_category: category?._id,
                img: '',
                manysize: true,
                name: products[i]['name'].trim(),
                time_warning: 0,
                type: 1,
                properties: [],
                toppings: [],
                subs: subs.map(item => {
                  let [name, price] = item.split('-');
                  return {
                    name: name.trim(),
                    // allow_sell: true,
                    delete_hidden: false,
                    selling: false,
                    units: [{
                      barcode: '',
                      default: true,
                      price: parseFloat(price.trim()),
                      price2: parseFloat(price.trim()),
                      ratio: 1,
                      unit: products[i]['unit'] ? products[i]['unit'].trim() : '',
                    }]
                  }
                })
              });
            }
          }
        }

        // Mỗi lần add 5 cái, xong mới add tiếp 5 cái kế
        const batchSize = 5;
        let addedProducts = [];

        // add KHÔNG có subs
        for (let i = 0; i < dataProducts.length; i += batchSize) {
          // lấy 5 cái
          const batch = dataProducts.slice(i, i + batchSize);
          try {
            const promises = batch.map(async (product) => {
              return await this.vhQueryCafe.addProducts([product]);
            });
            // add
            const results = await this.vhQuery.asyncPromiseAll(promises);
            // sau khi add xong
            console.log(`Batch ${Math.floor(i / batchSize) + 1} added successfully`);
            results.forEach((products, index) => {
              if (products) {
                addedProducts.push(...products); // Thêm các sản phẩm đã thêm vào mảng addedProducts
              } else {
                throw Error("Services have been created failed");
              }
            });
          } catch (error) {
            console.error(`Error in batch ${Math.floor(i / batchSize) + 1}:`, error);
          }
        }

        // add CÓ subs
        for (let i = 0; i < dataProductsSubs.length; i += batchSize) {
          // lấy 5 cái
          const batch = dataProductsSubs.slice(i, i + batchSize);
          try {
            const promises = batch.map(async (product) => {
              const subs = [...product.subs];
              const productToAdd = { ...product };
              delete productToAdd.subs;
              return await this.vhQueryCafe.addProductAndSubsProduct_Cafe(productToAdd, subs);
            });
            // add
            const results = await this.vhQuery.asyncPromiseAll(promises);
            // sau khi add xong
            console.log(`Batch ${Math.floor(i / batchSize) + 1} added successfully`,results);
            addedProducts = [...addedProducts, ...results]; // Thêm các sản phẩm đã thêm vào mảng addedProducts
            if (!results) {
              throw Error("Services have been created failed");
            }
          } catch (error) {
            console.error(`Error in batch ${Math.floor(i / batchSize) + 1}:`, error);
          }
        }
        // add xong
        this.showLog([...dataProducts, ...dataProductsSubs], [...addedProducts]);
      } 
      catch (error) {
        console.error(error)
        this.vhComponent.showToast(1500, `${this.languageService.translate("An error occurred while adding product. Please try again")}`, "success-toast")
      }

      this.vhComponent.hideLoading(0);
    });
  }



  showLog(data: any[], dataSuccess: any[]) {
    this.createdError = ""
    if (data.length != dataSuccess.length) {
      for (let product of data) {
        if (!dataSuccess.some(item => item.phone == product.phone))
          this.createdError += `<span> - ${product.name}(${product.phone})</span><br/>`
      }


      this.nzModalService.error({
        nzTitle: this.languageService.translate("Dishes have been created failed"),
        nzContent: this.createdError
      });

      this.formData = []

    } else {
      this.vhComponent.showToast(1500, `${this.languageService.translate("Dishes")} ${this.languageService.translate("has been added successfully")}`, "success-toast")
      this.goBack()
    }
  }

  fileFail() {
    this.vhComponent.alertMessage(this.languageService.translate("Error"), "", this.languageService.translate("Fail file format"), this.languageService.translate("Cancel")).then(() => this.formData = [])
  }
  /**
   * bắt sk thay đổi tên món, kiểm tra đã tồn tại món này trong db chưa
   */
  checkModelChange() {
    this.formData.forEach((form, index) => {
      let status: any = this.checkName(form['value']['name'], form['value']['id'])
      form.controls['status_name'].setValue(status[0] ? true : status[1])
    })
  }

  /**Check name và lấy id_product*/
  checkName(name, id_form?: any) {
    if (name != "") {
      // điều kiện check
      //-----CHECK NGANG-------//
      let checkDBName: any = this.localProduct.find(item => item.name.trim() == name.toString().trim());
      //-----CHECK DỌC-----//
      let checkName: any = this.formData.filter(item => item['value'].name == name && item['value']['id'] != id_form).length == 0 ? false : true;

      return [checkDBName ? checkDBName['_id'] : null, checkName]
    } else return [null];
  }

  /** Xuất ra file CSV hoặc tải về file mẫu */
  status: boolean = false;
  exportXLSXFile() {
    if (this.formData.length) {
      let XLSXData: any[] = [];
      this.formData.forEach((item, index) => {
        let row: Object = new Object;
        row[this.languageService.translate("_name_dishes")] = item['value']['name']
        row[this.languageService.translate("_category")] = item['value']['category']
        row[this.languageService.translate("_price")] = item['value']['price']
        XLSXData.push(row)
        if (index == this.formData.length - 1) {
          this.vhAlgorithm.exportXLSX(XLSXData, this.nameFile).then(() => this.status = true).catch(err => this.status = true)
        }
      })
    }
  }
}
